import React, {useState,useEffect} from 'react';
import { loadTweets,createTweet,apiTweetAction } from '../lookup';

export function ActionBtn(props) {
    const {tweet,action} = props
    const [likes, setLikes] = useState(tweet.likes ? tweet.likes : 0)
    const [userLike,setUserLike] = useState(tweet.userLike === true ? false : false)
    const className = props.className ? props.className : "btn btn-primary btn-sm"
    const actionDisplay = action.display ? action.display : "Action"

    const handleActionBackendEvent = (response,status)=>{
        console.log(status,response)
        if (action.type === 'like'){
            if (userLike === true){
                //set to unlike
                setLikes(likes - 1)
                setUserLike(false)
            } else {
                setLikes(likes + 1)
                setUserLike(true)
            }
        }
    }

    const handleClick = (event) =>{
        event.preventDefault()
        apiTweetAction(tweet.id, action.type,handleActionBackendEvent)
        
    }
    const display = action.type === 'like' ? `${likes} ${actionDisplay}` : actionDisplay
    return <button className={className} onClick={handleClick}>{display} </button>
}
  
  
export function Tweet(props){
    const {tweet} = props
    const className = props.className ? props.className : "col-10 mx-auto col-6"
    return <div className={className}>
      <p>{tweet.id}-{tweet.content}</p>
      <div className="btn btn-group">
        <ActionBtn tweet={tweet} action={{type:"like",display:"Likes"}} className="btn btn-primary"/>
        <ActionBtn tweet={tweet} action={{type:"unlike",display:"Unlike"}} className="btn btn-primary"/>
        <ActionBtn tweet={tweet} action={{type:"retweet",display:"retweet"}} className="btn btn-primary"/>
      </div>
    </div>
}


export function TweetsComponent(props){
    console.log("TweetsComponent props",props)
    const textAreaRef = React.createRef()
    const [newTweets, setNewTweets] = useState([])
    
    
    
    const handleBackendUpdate = (response,status)=>{
        let tempNewTweets = [...newTweets]
        if (status === 201){
            tempNewTweets.unshift(response)
            setNewTweets(tempNewTweets)
        } else {
            console.log(response)
            alert("An error occured")
        }
    }

    const handleSubmit = (event) =>{
        event.preventDefault()
        const newVal = textAreaRef.current.value
        // console.log("newVal",newVal)
        createTweet(newVal, handleBackendUpdate)
        textAreaRef.current.value = ""
    }
    

    return <div className={props.className}>
            <div className="col-12 mb-3">
            <form onSubmit={handleSubmit}>
                <textarea ref={textAreaRef} required={true} className='form-control'>

            </textarea>
            <button type="submit" className="btn btn-primary my-3">Tweet</button>
        </form>
        </div>
        <TweetsList newTweets={newTweets} />
    </div> 
}


export function TweetsList(props) {
    console.log("TweetsList props",props)
    const [tweetsInit,setTweetsInit] = useState([])
    const [tweets,setTweets] = useState([])
    const [tweetsDidSet, setTweetsDidSet] = useState([])
    //console.log(props.newTweets)
    
    useEffect(()=>{
        const final = [...props.newTweets].concat(tweetsInit)
        if (final.length !== tweets.length){
            setTweets(final)
        }
    },[props.newTweets, tweets, tweetsInit])

    useEffect(()=>{
        if (tweetsDidSet === false){
            const myCallback = (response,status)=>{
              const data = response.results
              console.log(response,status,'data',data)
              if (status === 200){
                  setTweetsInit(data)
                  setTweetsDidSet(true)
              }
            }
            loadTweets(myCallback)
        }
    },[tweetsInit,tweetsDidSet,setTweetsDidSet])

    return tweets.map((item,idx)=>{
        return <Tweet tweet={item} className='my-5 py-5 border bg-white text-dark'/>
    })
}